/* BUTTON */

.btn {
  margin: 1rem 1rem;
  -webkit-appearance: none;
  border: 6px solid rgba(216, 205, 205, 0.45);
  border-radius: 50px;
  padding: 0.5em 1.5em;
  background-repeat: no-repeat;
  background-size: 100%;
  background-clip: padding-box;
  position: relative;
  color: rgb(182, 107, 231);
  cursor: pointer;
}

.btn:hover {
    color: rgb(245, 113, 175);
    font-weight: bold;
}
.btn:before {
  content: '';
  width: 100%;
  height: 100%;
  top: 5px;
  left: 5px;
  border-radius: 50px;
  z-index: -1;
  -webkit-filter: blur(14px);
          filter: blur(14px);
}
.btn:after {
  content: '';
  width: 100%;
  height: 100%;
  top: 5px;
  right: 5px;
  border-radius: 50px;
  z-index: -1;
  -webkit-filter: blur(14px);
          filter: blur(14px);
}

